import { Component, OnInit } from '@angular/core';
import { InternalTeamService } from 'src/app/core/services/internal-team/internal-team.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-manage-admin',
  templateUrl: './manage-admin.component.html',
  styleUrls: ['./manage-admin.component.scss'],
})
export class ManageAdminComponent implements OnInit {
  arrInternal: any = [];

  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  constructor(
    private internalTeamService: InternalTeamService,
    private pageServiceObj: PagerService
  ) {}

  ngOnInit() {
    this.getAllAdmin();
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllAdmin();
  }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrInternal.length
    );
  }
  getAllAdmin() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,

      fkAdminId: '',
      strStatus: '',
      strUserType: '', //1 or 2
    };

    this.fetchLoading = true;
    this.internalTeamService.getAllAdmin(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrInternal = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrInternal = [];
        // alert(res.message);
      }
    });
  }

  deleteAdmin(id) {
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#d33',
      reverseButtons: true,
      confirmButtonText: 'Yes, delete it!',
    }).then((result) => {
      if (result.isConfirmed) {
        const obj = {
          strLoginUserId: localStorage.getItem('userId'),
          fkAdminId: id,
          strStatus: 'D', //n for approve and p for pending . default its p
        };

        this.internalTeamService
          .deleteInternalTeamAdmin(obj)
          .subscribe((res) => {
            if (res.success) {
              Swal.fire({
                title: 'Deleted!',
                text: 'Your file has been deleted.',
                icon: 'success',
              });
              this.getAllAdmin();
            } else {
              // alert(res.message);
            }
          });
      }
    });
  }
}
