import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { CommonService } from '../common/common.service';
import { ApiResponse } from '../../model/api-response.models';
import { catchError, retry } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class InternalTeamService {
  private apiURL: string = environment.API_ENDPOINT;

  constructor(private http: HttpClient, private commonService: CommonService) {}

  httpOptions = {
    headers: new HttpHeaders({
      Authorization: 'bearer ' + localStorage.getItem('token'),
    }),
  };

  getAllAdminModules() {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'admin/get_admin_modules',
        {},
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  addInternalTeamAdmin(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'admin/add_admins',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  getAllAdmin(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'admin/get_all_admins',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  updateInternalTeamAdmin(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'admin/update_admins',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
  deleteInternalTeamAdmin(obj: any) {
    return this.http
      .post<ApiResponse>(
        this.apiURL + 'admin/update_admins',
        obj,
        this.httpOptions
      )
      .pipe(retry(1), catchError(this.commonService.handleError));
  }
}
